.song {
  padding: 15px;
  border-radius: 15px;
  margin: 10px;
  width: 400px;
  box-shadow: 40px 40px 120px #bbb inset;
}

.itunes {
  background-image: url("./BG.jpg");
  min-height: 100%;
  padding: 10px;
  background-repeat: no-repeat;
}

.trackInfo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
  color: #222;
  font-size: 15px;
}

.trackInfo > div {
  margin: auto;
}

.song audio {
  width: 100%;
  border-radius: 12px;
  margin-top: 10px;
}

.songs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 20px;
  justify-items: center;
}

.itunes form {
  display: flex;
  justify-content: center;
  margin: 5px;
}

.itunes input {
  padding: 6px;
  font-size: 16px;
  border: 2px #aaa solid;
  border-radius: 12px;
  background-color: transparent;
  color: #ddd;
}

.itunes button {
  background-color: transparent;
  margin-left: 5px;
  border-radius: 5px;
  padding: 7px 15px 7px 15px;
  letter-spacing: 1px;
  color: white;
  font-size: 17px;
  opacity: 0.8;
}

.itunes button:hover {
  transition: 200ms;
  cursor: pointer;
  opacity: 1;
}

.itunes p {
  text-shadow: 0px 0px 15px #fff;
  font-size: 23px;
  color: #ccc;
}

.itunes span {
  display: block;
  color: #fff;
  margin-top: 25px;
  font-size: 19px;
}

.pagination button {
  box-shadow: 2px 2px 20px #222 inset;
  margin: 20px 40px;
  width: 120px;
  height: 40px;
  font-size: 19px;
  text-shadow: 2px 2px 10px #111;
}
